  font-family: 'Protest Riot', sans-serif;
  font-weight: 400;
  font-style: normal;
}

/*
  Josh's Custom CSS Reset
  https://www.joshwcomeau.com/css/custom-css-reset/
*/
*,
*::before,
*::after {
  box-sizing: border-box;
}
* {
  margin: 0;
}
body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}
input,
button,
textarea,
select {
  font: inherit;
}
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}
#root,
#__next {
  isolation: isolate;
}

* {
  box-sizing: border-box;
}

:root {
  color: black;
  font-size: 18px;
  font-family: 'Protest Riot', sans-serif;
}

a {
  text-decoration: none;
  color: inherit;
}

body {
  display: flex;
  flex-direction: column;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  min-height: 100vh;
  margin: 0;
}

.header-wrapper {
  background-color: white;
  display: flex;
  justify-content: flex-start;
  gap: 3vw;
  padding-top: 2vh;
  padding-bottom: 2vh;
  padding-left: 4ch;
}

.header-bar {
  /* Remove default button styles */
  background: none;
  border: none;
  padding: 0;
  margin: 0;
  cursor: pointer; /* Add pointer cursor to indicate interactivity */

  /* Adjust font styles */
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  text-decoration: underline; /* Add underline for text effect */
}

/* Style when button is hovered */
.header-bar:hover {
  color: blue; /* Change color on hover, adjust as needed */
}

main {
  display: flex;
  flex-grow: 1;
  align-self: stretch;
  flex-direction: column;
  align-items: center;
}

.main-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  flex-grow: 1;
  width: 80vw;
  background-color: orangered;
  padding-top: 2vh;
  padding-bottom: 2vh;
  gap: 2vh;
}

#contact-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  gap: 2vh;
}

.info-card,
.contact-item {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background-color: yellow;
  color: black;
  text-align: left;
  width: 60vw;
  height: auto;
  border-radius: 12px;
  padding: 1vh 2vw 2vh 1vw;
}

.menu-item {
  background-color: yellow;
  color: black;
  text-align: left;
  padding: 2ch 4ch 2ch 4ch;
  width: 60vw;
  height: auto;
  border-radius: 12px;
}

#title {
  font-size: 3rem;
  text-align: center;
}

.footer-wrapper {
  background-color: gainsboro;
  display: flex;
  justify-content: space-around;
  font-size: 0.6rem;
  padding-top: 1vh;
  padding-bottom: 1vh;
}
